import { request } from 'utils';

const prefix = '/api/organization';

export const getRankConfigs = ({
  id,
  label,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  request({
    prefix,
    url: '/RankConfig',
    method: 'GET',
    params: {id,label,page,pageSize,orderBy,isPagingEnabled},
    data: {}
  });

  



