import { request } from 'utils';

const prefix = '/api/organization';

export const getSalaryFrequencyConfigs = ({
  positionName,
  frequencyYear,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  request({
    prefix,
    url: '/SalaryFrequencyConfig',
    method: 'GET',
    params: {
      positionName,
      frequencyYear,
      page,pageSize,
      orderBy,
      isPagingEnabled
    },
    data: {}
  });




